import React, { useEffect } from "react";
import { connect } from "react-redux";
import {changeTheMode} from "./redux-store/redux/ModeActionCreator";
import darkMode from "./../Images/darkMode.png";
import lightMode from "./../Images/lightMode.png";
import { useState } from "react";
import "./ToggleMode.css"

function ToggleMode(props) {
  console.log('p',props.mode)
    
  let [toggleMode, setToggleMode] = useState(props.mode);
  
  useEffect(()=>{
    setToggleMode(props.mode)
  },[props.mode])

  return (
    <div>
      <div className="toggleDiv">
        <div>
          {toggleMode === "darkMode" && (
            <img
              src={lightMode}
              alt="darkModeImage"
              className="modeImages"
              onClick={() => props.changeTheMode("lightMode")}
            />
          )}
          {toggleMode === "lightMode" && (
            <img
              src={darkMode}
              alt="lightModeImage"
              className="modeImages"
              onClick={() => props.changeTheMode("darkMode")}
            />
          )}
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  // console.log('s',state.mode.mode)
  return {
    mode: state.mode.mode,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    changeTheMode: (toggleMode) => dispatch(changeTheMode(toggleMode)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ToggleMode);

