import React, { useState } from "react";
import { useEffect } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import "./Navbar.css";

function Navbar(props) {
  console.log(props);
  const [expandShrink, setExpandShrink] = useState(true);
  const [mode, setMode] = useState(props.mode);

  useEffect(() => {
    setMode(props.mode);
  }, [props.mode]);

  const toggle = () => {
    if (expandShrink) {
      setExpandShrink(false);
    } else {
      setExpandShrink(true);
      console.log(expandShrink);
    }
  };

  return (
    <div className="App">
      <nav
        className={`navbar navbar-expand-lg bg-${
          mode === "lightMode" ? "light" : "dark"
        }`}
      >
        <div className="container-fluid ms-3">
          <a
            className="navbar-brand nav-brand-link"
            href="/RestaurantServiceForm"
          >
            Restaurant FeedBack Form
          </a>
          {/* <span style={{color:"white"}}>hi {name}</span> */}
          <button
            onClick={toggle}
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div
            className="collapse navbar-collapse"
            style={{ display: expandShrink ? "none" : "block" }}
            id="navbarSupportedContent"
          >
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  aria-current="page"
                  to="/RestaurantServiceForm"
                >
                  <span
                    style={{
                      color:
                        mode === "lightMode"
                          ? "black"
                          : "white",
                    }}
                  >
                    FeedBackFrom
                  </span>
                </Link>
              </li>
              <li
                className="nav-item"
              >
                <Link className="nav-link" to="/feedbacktable">
                  <span
                    style={{
                      color:
                        mode === "lightMode"
                          ? "black"
                          : "white",
                    }}
                  >
                    FeedBackTable
                  </span>
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    mode: state.mode,
  };
};

export default connect(mapStateToProps)(Navbar);
